:root {
  --primary-text-color: #2c2c2c;
  --secondary-text-color: #000;
  --accent-color: #7eb101;
  --green-background: #bbf330;
  --yellow-main-color: #ffe550;
  --white-main-color: #fefefe;
  --timing-function: 250ms cubic-bezier(.4, 0, .2, 1);
