.page-header {
    padding-top: 10px;
    padding-bottom: 10px;
    box-shadow: 0px 4px 24px rgba(105, 105, 105, 0.1);
}
.page-header__wrapper{
    
    display: flex;
    align-items: center;
    
    @include for-phone-only{
        display: flex;
        justify-content: space-between;
    }
}
.navigation-wrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
    align-items: center;
    flex-grow: 1;

}
.logo{
    display: block;
    
}

.logo__icon {
    width: 71px;
    height: 52px;

}



.navigation-menu__list{
    display: none;
    @include for-tablet-up{
    display: flex;
    gap: 28px;
    justify-content: flex-end;
    margin-right: 28px;
    }
    
}

.navigation-menu__link{
    @include font (400, 14px, 16px);
    color: var(--primary-text-color);
    transition: color var(--timing-function);
    &:hover,
    &:focus{
        color: var(--accent-color);
    }
}
.page-header__order-btn{
    display: none;
    @include for-tablet-up{
    display: flex;
    padding: 7px 17px;
    background-color: var(--green-background);
    border: transparent;
    border-radius: 30px;
    transition: background-color var(--timing-function);
    @include font (400, 14px, 21px);
    &:hover,
    &:focus{
        background-color: #B5FF00;
        }
    }
    
}




@include for-tablet-up{
    .mobile-menu{
        display: none;
    }
}


.mobile-menu {
        position: relative;

    }
   

.mobile-menu__list {
    position: absolute;
    top: 100%;
    right: 20px;
    width: 130px;
    height: 255px;
    z-index: 100;
    opacity: 0;
    background-color: var(--white-main-color);
    border-radius: 30px;
    border-top-right-radius: 0;
    box-shadow: 0px 4px 24px rgba(105, 105, 105, 0.1);
    visibility: hidden;
    pointer-events: none;
    transition: opacity var(--timing-function),
                visibility var( --timing-function);


}
.mobile-menu__item {
    padding: 16px 10px 16px 16px;
    transition: background-color var(--timing-function);
    &:first-child{
        border-top-left-radius: 30px;
    }
    &:last-child{
        border-radius: 0 0 30px 30px;
    }
    
    &:hover{
        background-color: var(--green-background);
    }
}

.mobile-menu__link{
    color: var(--secondary-text-color);
    @include font (400, 16px, 19px)
}

.mobile-menu__list.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
}




.mobile-menu__burger-btn{
    background-color: transparent;
    border: 0;
    padding-right: 20px;
    @include for-tablet-up{
        display: none;
    }
}

.mobile-menu__burger-icon{
    width: 28px;
    height: 28px;
    
}

 